.BuildControls{
    width: 100%;
    background-color: #cf8f2e;
    text-align: center;
    display: flex;
    flex-flow: column;
    box-shadow: 0 5px 4px #ccc;
    margin: auto;
    padding: 10px 0;
}

.BuildControls .OrderButton{
    width: 200px;
    margin: 10px auto;
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #966909;
}

.BuildControls .OrderButton:hover, .BuildControls .OrderButton:active{
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

.BuildControls .OrderButton:disabled{
    background-color: #C7C6c6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.BuildControls .OrderButton:not(:disabled){
    animation: enable 0.3s linear;
}

@keyframes enable{
    0%{
        transform: scale(1);
    }
    60%{
        transform: scale(1.1);
    }
    100%{
        transform: scale(1);
    }
}
