.Button{
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    font-weight: bold;
}

.Button:first-of-type{
    margin-left: 0;
    padding-left: 0;
}

.Success{
    color: #5C9210;
}

.Danger{
    color: red;
}